.swash-background {
    position: relative;
    width: 100%;
    background: linear-gradient(135deg, #34d399, #0369a1);
    overflow: visible;
    padding-top: 4rem;
  
    .swash-shape {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 140px;
      background: white;
      mask-image: url('data:image/svg+xml;utf8,<svg viewBox="0 0 1600 140" xmlns="http://www.w3.org/2000/svg"><path fill=\"black\" d=\"M1600 0H0v40c137.6 52.6 360 80 800 80s662.4-27.4 800-80V0z\"/></svg>');
      mask-size: cover;
      mask-repeat: no-repeat;
      mask-position: top center;
      -webkit-mask-image: url('data:image/svg+xml;utf8,<svg viewBox="0 0 1600 140" xmlns="http://www.w3.org/2000/svg"><path fill=\"black\" d=\"M1600 0H0v40c137.6 52.6 360 80 800 80s662.4-27.4 800-80V0z\"/></svg>');
      -webkit-mask-size: cover;
      -webkit-mask-repeat: no-repeat;
      -webkit-mask-position: top center;
      z-index: 1;
    }
  
    .swash-inner {
      position: relative;
      z-index: 2;
    }
}
  